<template>
  <div class="pem-header" ref="header">
    <div v-if="left === 'back'" class="pem-menu pem-left" @click="back">
      <pem-icon name="back" width="48" height="48" />
    </div>
    <div v-if="left === 'close'" class="pem-menu pem-left" @click="back">
      <pem-icon name="close" width="48" height="48" />
    </div>
    <router-link tag="div" v-if="left === 'backHome'" class="pem-menu pem-left" to="/">
      <pem-icon name="back" width="48" height="48" />
    </router-link>
    <router-link tag="div" v-if="left === 'download'" class="pem-menu pem-left" to="/download">
      <pem-icon name="back" width="48" height="48" />
    </router-link>
    <h1 class="text">{{ title }}</h1>
    <slot></slot>
    <router-link tag="div" v-if="right === 'home'" class="pem-menu pem-right" to="/my-home">
      <pem-icon name="my" width="46" height="46" />
      <span v-if="hasMsg" class="unread"></span>
    </router-link>
    <router-link tag="div" v-if="right === 'login'" class="pem-menu pem-right" to="/login">
      <pem-icon name="my" width="46" height="46" />
    </router-link>
    <router-link tag="div" v-if="right === 'auth'" class="pem-menu pem-right" to="/auth">
      <pem-icon name="my" width="46" height="46" />
    </router-link>
  </div>
</template>

<script type="text/ecmascript-6">
import PemIcon from 'components/pem-icon/pem-icon'

export default {
  components: {
    PemIcon
  },
  props: {
    title: {
      type: String,
      default: ''
    },
    left: {
      type: String,
      default: ''
    },
    right: {
      type: String,
      default: ''
    },
    isTransparent: {
      type: Boolean,
      default: false
    },
    hasMsg: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    back () {
      this.$router.goBack()
    },
    setBgColor (color) {
      this.$refs.header.style.backgroundColor = color
    },
    setFontColor (color) {
      this.$refs.header.style.color = color
    },
    hide () {
      this.$refs.header.style.display = 'none'
    },
    show () {
      this.$refs.header.style.display = 'block'
    }
  }
}
</script>



