<template>
  <div class="details full-screen">
    <pem-header ref="headerNav" left="back">{{ project.name }}</pem-header>
    <div class="page-panel">
      <pem-scroll-view ref="scroll" class="scroll-view" :listen-scroll="listenScroll" :probeType="probeType" @scroll="scroll" v-if="!isInit">
        <div>
          <div class="details-wrapper-header" ref="detailsHeader">
            <img class="details-info-logo" :src="project.logo" @error="defaultImage"/>
            <div class="details-info-bar">
              <div class="details-info-name">{{ project.name }}</div>
              <div class="details-info-desc">
                <span v-if="project.field">{{ project.field }}</span>
                <span v-if="project.field && project.place"> | </span>
                <span v-if="project.place">{{ project.place }}</span>
              </div>
              <div class="details-info-project">
                <span>{{ project.desc }}</span>
              </div>
            </div>
          </div>
          <pem-card title="行业标签" line size='18' v-if="project.projtags && project.projtags.list.length > 0">
            <pem-card-content :list="project.projtags.list"></pem-card-content>
          </pem-card>
          <pem-card title="基础信息" line size='18' v-if="project.base && project.base.list.length > 0">
            <pem-card-content :desc="project.base.desc" :list="project.base.list"></pem-card-content>
          </pem-card>
          <pem-card title="团队" line size='18' v-if="project.team && project.team.list.length > 0">
            <pem-card-content :desc="project.team.desc" :list="project.team.list" style="padding-top: 0"></pem-card-content>
          </pem-card>
          <pem-card title="历史融资数据" line size='18' v-if="project.history && project.history.list.length > 0">
            <pem-card-content :desc="project.history.desc" :list="project.history.list" size="70"></pem-card-content>
          </pem-card>
          <div class="filler"></div>
        </div>
      </pem-scroll-view>
    </div>
    <div class="details-button">
      <pem-button text="认领项目" @submit="submit" class="submit" />
    </div>
    <pem-dialog ref='dialog' text="您需要登录APP，获取更多权益。" confirmBtnText="我知道了" type="alert"></pem-dialog>
  </div>
</template>

<script type="text/ecmascript-6">
import PemHeader from 'components/header/header'
import PemScrollView from 'components/scroll-view/scroll-view'
import PemCard from 'components/card/card'
import PemIcon from 'components/pem-icon/pem-icon'
import PemDialog from 'components/dialog/dialog'
import PemButton from 'components/pem-button/pem-button'
import PemCardContent from 'components/card-content/card-content'
import { MiniSiteApi } from 'api'
import { dateFormat, setTitle } from 'common/js/util'

// 临时 后期要删除
import tempLogoUrl from 'common/image/icon-project-Default_graph@2x.png'
import tempAvatarUrl from 'common/image/icon-default-avatar@2x.png'

export default {
  components: {
    PemHeader,
    PemScrollView,
    PemCard,
    PemIcon,
    PemDialog,
    PemButton,
    PemCardContent
  },
  data () {
    return {
      name: '',
      isInit: true,
      project: {}
    }
  },
  created () {
    this._setTitle('项目工场-项目详情')
    this.probeType = 3
    this.listenScroll = true
    setTimeout(() => {
      this._initData()
    }, 20)
  },
  methods: {
    _setTitle (title) {
      setTitle(title)
    },
    _initData () {
      this.isInit = true
      this.headerHeight = 140
      this.$refs.headerNav.setBgColor('transparent')
      this.$refs.headerNav.setFontColor('transparent')
      this.getDetail()
    },
    scroll (pos) {
      if (pos.y > 0.01) {
        this.$refs.headerNav.hide()
      } else {
        this.$refs.headerNav.show()
      }
      if (pos.y >= 0) {
        this.$refs.headerNav.setBgColor('transparent')
        return false
      }
      this.$refs.headerNav.setBgColor(`rgba(41, 41, 41, ${(pos.y / this.headerHeight < -1) ? 1 : Math.abs(pos.y / this.headerHeight)})`)
      this.$refs.headerNav.setFontColor(`rgba(255, 255, 255, ${(pos.y / this.headerHeight < -1) ? 0.97 : 0})`)
    },
    submit () {
      this.$refs.dialog.show()
    },
    getDetail () {
      let _project = {}
      this._getDetail({
        projectzj: this.$route.params.zj.toString()
      }, (data) => {
        if (+data.resultcode === 1) {
          this.name = data.data.projname
          _project.name = data.data.projname
          _project.summary = data.data.slogan
          _project.logo = data.data.logourl || tempLogoUrl
          _project.desc = data.data.slogan
          _project.place = data.data.location.pop()
          _project.field = data.data.industry.join('、')
          // 行业标签
          _project.projtags = {
            list: []
          }
          if (data.data.projtags) {
            _project.projtags.list.push({
              list: data.data.projtags || [],
              type: 'tags'
            })
          }
          // 基础信息
          _project.base = {
            desc: null,
            list: []
          }
          if (data.data.projdesc) {
            _project.base.list.push({
              title: '项目介绍',
              content: data.data.projdesc || '',
              type: 'text'
            })
          }
          if (data.data.corpname) {
            _project.base.list.push({
              title: '公司名称',
              content: data.data.corpname || '',
              type: 'text'
            })
          }
          // 团队
          _project.team = {
            desc: null,
            list: []
          }
          if (data.data.staffs && data.data.staffs.length > 0) {
            _project.team.list = data.data.staffs.map(item => {
              return {
                username: item.staffname || '',
                avatar: tempAvatarUrl,
                position: item.staffposition || '',
                desc: item.staffdesc || '',
                type: 'member'
              }
            }) || []
          }
          // 历史
          _project.history = {
            desc: null,
            list: []
          }
          if (data.data.invests && data.data.invests.length > 0) {
            let _content = data.data.invests.map(item => {
              return {
                left: {
                  title: item.phase || '',
                  desc: item.investdate && dateFormat(item.investdate || null, 'yyyy-MM-dd')
                },
                right: {
                  title: item.amount || '',
                  desc: (item.investors || []).join('、')
                }
              }
            }) || []
            _project.history.list.push({
              title: null,
              content: _content,
              type: 'timeline'
            })
          }
          this.project = _project
          this.isInit = false
          this.wxShare({
            title: '【' + this.project.name + '】' + this.project.summary,
            content: this.project.desc,
            imgUrl: this.project.logo,
            linkUrl: window.location.href.replace(/\?.*/, '').replace('details/', 'share?projectzj=')
          })
        }
      })
    },
    _getDetail (options, callback) {
      MiniSiteApi.findProjectDetail(options).then(data => {
        callback(data)
      })
    },
    defaultImage (e) {
      e.target.src = tempLogoUrl
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"
  .details
    font-size: 0.24rem
    .page-panel
      margin-top: -0.88rem
    .details-wrapper-header
      width: 7.5rem
      height: 3.42rem
      bg-image(image-project-background)
      background-size: 7.5rem 3.52rem
      background-position: center bottom
      position: relative
      .details-info-logo
        width: 1.08rem
        height: 1.08rem
        border-radius: 0.08rem
        position: absolute
        left: 0.3rem
        top: 1.2rem
      .details-info-bar
        position: absolute
        top: 1.2rem
        left: 1.68rem
        .details-info-name
          color: $color-text-title-inverse
          font-size: 0.36rem
          line-height: 0.48rem
          letter-spacing: -0.0072rem
          margin-bottom: 0.1rem
        .details-info-desc
          font-size: 0.28rem
          line-height: 0.34rem
          letter-spacing: -0.0016rem
          color: $color-text-body-inverse
          margin-bottom: 0.28rem
        .details-info-project
          padding-right: 0.3rem
          span
            font-size: 0.24rem
            line-height: 0.34rem
            color: $color-text-body-inverse
    .details-button
      position: fixed
      left: 0
      right: 0
      bottom: 0
      z-index: 10
      background-color: $color-panel
      height: 1.4rem
      border-top: 0.02rem solid #f3f3f3
      .submit
        margin-top: 0.3rem
</style>



